import React from "react";
import axios from "axios";
import BlogCard from "../BlogCard"
import BlogPost from "../BlogPost"
// import "../../css/blog.css"

class Blog extends React.Component {
    _isMounted = false;

    constructor() {
        super();
        this.state = { blogPosts: [], currentPost: [], on: false, scrollPosition: 0 }
        this.reset = this.reset.bind(this)
    }

    componentDidMount = async () => {
        this._isMounted = true;

        const response = await axios({
            method: "GET",
            url: "https://elinlaxdalbackend.herokuapp.com/blog-posts"
        })
        if (this._isMounted) {
            this.setState({ blogPosts: response.data.reverse() })
        }

        this.adjustFooterBottom();
    }


    componentWillUnmount = () => {
        this._isMounted = false;
        const footer = document.querySelector("footer");
        footer.style.bottom = "-20em";
    }

    adjustFooterBottom = () => {
        const footer = document.querySelector("footer");
        footer.style.bottom = "-6em";
    }

    renderPost(post) {
        if (this._isMounted) {
            this.setState({ currentPost: post, on: !this.state.on })
            document.querySelector("footer").style.visibility = "hidden";
            const scrollPosition = document.documentElement.scrollTop || document.body.scrollTop;
            this.setState({ scrollPosition })
        }
    }

    reset() {
        if (this._isMounted) {
            this.setState({ on: !this.state.on })
            this.setState({ currentPost: [] })
            document.querySelector("footer").style.visibility = "visible";
            document.documentElement.scrollTop = document.body.scrollTop = this.state.scrollPosition;
            this.setState({ scrollPosition: 0 })
        }
    }





    render() {
        return (
            <div className="blog-container hideThis">
                {this.state.blogPosts.map(post =>
                    <div key={post.id} className={post.title} onClick={() => this.renderPost(post)}>
                        <BlogCard key={post.id} pic={post.picture.formats.large.url} title={post.title} body={post.body} />
                    </div>
                )}
                <div className="">
                    {this.state.on ? <BlogPost title={this.state.currentPost.title}
                        body={this.state.currentPost.body}
                        pic={this.state.currentPost.picture.formats.large.url}
                        reset={this.reset} /> : ""}
                </div>
            </div>

        )
    }
}

export default Blog;